tent: \"\";\n  position: absolute;\n  top: -0.5px;\n  left: -0.5px;\n  width: 100.5%;\n  height: 100.5%;\n  border-radius: 25px;\n  background: linear-gradient(to right, rgba(40,40,60,1) 0%,rgba(40,40,60,0.5) 90%);\n  background-blend-mode: multiply;\n  will-change: transform;\n  z-index: 0;\n}\n\n.resto-card i {\n  font-size: .7em;\n  color: #ff5b84;\n}\n\n.resto-card h1 {\n  font-size: 3rem;\n  position: relative;\n  z-index: 10;\n  margin-bottom: 5px;\n}\n\n.resto-card a{\n  text-decoration: none;\n  font-size: 3rem;\n}\n\n.resto-detail ul li{\n  text-align: right;\n} \n\n.resto-card p {\n  position: relative;\n  text-align: left;\n  z-index: 10;\n  margin: 0.1rem 2rem;\n  font-size: 1.5em;\n  width: 80%;\n  height: 5%;\n}\n\n.resto-card span {\n  display: inline-block;\n  position: relative;\n  z-index: 5;\n  margin: auto 5rem;\n  color: rgb(210, 210, 210);\n  font-weight: 600;\n}\n\n/* Detail Resto */\n\n.resto-detail{\n  align-items: right;\n  display: flex;\n  background-color: #ffa559;\n  border-radius: 2rem;\n  color: #363232;\n  position: relative;\n  box-sizing: border-box;\n  width: 80%;\n  height: fit-content;\n  background-position: center;\n  margin: 3em auto; \n  border-radius: 25px;\n  \n  color: white;\n  \n}\n\n.resto-detail tb{\nwidth: 100%;\n\n}\n\n.resto-detail p {\n  padding: 1.5rem;\n  background-color: #363232;\n  color: #ffa559;\n  width:50 ;\n  font-size: 2rem;\n  text-align: right;\n  align-items: right;\n  z-index: 10;\n  margin: 10% 0;\n  border-radius: 50px 0 0 50px;\n}\n\n.resto-detail img{\n max-width: 60% ;\n border-radius: 0 3em 3em 0 ;\n}\n\n.resto-detail a{\n  font-size: 3rem;\n  position: absolute;\n  text-decoration: none;\n  padding: 1rem;\n  padding-left:10px ;\n  color: #363232;\n  background-color: #ffa559;\n  border-radius: 0 3em 3em 0 ;\n}\n\n/* resto-desc */\n\n.resto-desc{\n  margin: 2rem auto;\n  width: 80%;\n  align-items: center;\n  background-color: #ffa559;\n  padding: 2rem 3rem;\n  border-radius: 15px;\n  \n}\n\n.resto-desc p{\n  font-size: 2rem;\n  word-spacing: 5px;\n  text-align: justify;\n  color: #242222;\n}\n\n\n\n.like {\n  width: 5rem;\n  height: 5rem;\n  \n  align-items: center;\n  justify-content: center;\n  background-color: #f55151;\n  border-radius: 50%;\n  border: 0;\n  font-size: 2rem;\n  color: white;\n  cursor: pointer;\n}\n\n.like-container{\n  display: block;\n  background-color: #ffa559;\n  padding: 1rem 1rem;\n  width: fit-content;\n  align-items: center;\n  margin: auto;\n  border-radius: 2rem;\n}\n\n.fav-header{\n  color: #383736;\n  font-weight: 600;\n  margin: 0.5rem;\n  padding: 0.3rem;\n  font-size: 1.5rem;\n}\n\n.resto-menu-header{\n  display: flex;\n  margin: auto;\n  background-color: #383736;\n  width: 50%;\n  position: relative;\n  align-items: center;\n  padding: 1rem;\n  border-radius: 2rem 2rem 0 0;\n}\n\n.resto-menu-header h1{\n  font-size: 25px;\n  margin: auto;\n}\n\n/* #menu-container {\n  display: flex;\n  margin: 0 auto 2rem auto;\n  max-width: 50%;\n  background-color: #ffa559;\n  color: #363232;\n}\n\n#food-container{\n  display: flex;\n  margin: auto;\n  background-color: #383736;\n  width: 50%;\n  position: relative;\n  align-items: center;\n  padding: 1rem;\n}\n\n.food-item {\n  color: #363232;\n  font-weight: 600;\n  text-align: center;\n  border-radius: 5px;\n  min-width: 100%;\n}\n\n#drink-container{\n  display: flex;\n  margin: auto;\n  background-color: #383736;\n  width: 50%;\n  position: relative;\n  align-items: center;\n  padding: 1rem;\n}\n\n.drink-item {\n  color: #363232;\n  font-weight: 600;\n  padding: 0.5rem 2rem;\n  margin: 0.5rem 2rem;\n  border-radius: 5px;\n  text-align: center;\n  min-width: 100%;\n} */\n\n#menu-container {\n  display: flex;\n  justify-content: center;\n  \n}\n\n#foods-container, #drinks-container {\n  display: flex;\n  flex-direction: column;\n  background-color: #ffa559;\n  color: #363232;\n  padding: 1rem 0.5rem;\n  margin: 0 0 3rem 0;\n  width: 25%;\n  font-weight: 600;\n  border-radius: 0 0 0 2rem;\n}\n\n#drinks-container{\n  border-radius: 0 0 2rem 0;\n}\n\n.food-item, .drink-item {\n  margin: 0.1rem auto;\n  border-radius: 5px;\n  color: #363232;\n  padding: 1rem 1.5rem;\n  font-size: 1.5rem;\n}\n\n.food-item:hover, .drink-item:hover{\n  background-color: #e47d29;\n  border-radius: 2rem;\n}\n\n.resto-review{\n  margin: auto;\n  background-color: #383736;\n  width: 50%;\n  position: relative;\n  align-items: center;\n  padding: 1rem;\n  border-radius: 2rem;\n  margin-bottom: 2rem;\n  \n}\n\n.customer-review{\n  margin: 2rem;\n  padding: auto;\n  background-color: #ffa559;\n  color: #383736;\n  width: auto;\n  border-radius: 3rem;\n}\n\n.review-text, .review-name{\n  color: #383736;\n\n}\n\n.review-name{\ntext-align: end;\npadding: 0 2rem 2rem 0 ;\nfont-size: 1.5rem;\n}\n\n.review-text{\n  font-size: 3rem;\n  padding: 1rem;\n}\n\n.resto-empty h2{\n  font-size: 3rem;\n}"],"sourceRoot":""}]);
